.container {
    margin: 50px 0;
}

.card {
    margin: 0 2% !important;
    background-color: rgba(250, 250, 250, 0.5) !important;
}

.infected {
    border-bottom: 10px solid rgb(255, 205, 0, 0.8);
}

.recovered {
    border-bottom: 10px solid rgb(0, 204, 0, 0.8);
}

.deaths {
    border-bottom: 10px solid rgb(255, 0, 0, 0.8);
}

@media (max-width: 770px) {
    .card {
        margin: 2% 0 !important;
    }
}